@import url(https://fonts.googleapis.com/css2?family=Tajawal&display=swap);
body {
  margin: 0px;
  font-family: 'Tajawal', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  min-height: 100vh;
  margin: 0px;
  align-items: center;
  background-color: #E0F2F1;
}

.PanelsWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;
}

.Panel {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 600px;
  padding: 10px;
  margin: 10px;
  background-color: #092532;
  border-radius: 5px;

}

.ButtonWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

@media screen and (max-width: 1100px) {
  .PanelsWrapper {
    flex-direction: column;
    align-items: center;
    margin-top: 0px;
  } 

  .Panel {
    height: 380px;
  }
}; 

.Header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%; 
  color: white;
  font-size: calc(40px + 2vmin);
  padding-top: 20px;
  font-weight: bold;
  text-shadow: 1.5px 1.5px 0 #333, 0px 1.5px 0 #333, -1.5px -1.5px 0 #333, -1.5px -1.5px 0 #333, -1.5px 1.5px 0 #333, 1.5px -1.5px 0 #333, 0.7778174593px 0.7778174593px 0 #aaaaaa, 1.5556349186px 1.5556349186px 0 #aaaaaa, 2.3334523779px 2.3334523779px 0 #aaaaaa, 3.1112698372px 3.1112698372px 0 #aaaaaa, 3.8890872965px 3.8890872965px 0 #aaaaaa, 4.6669047558px 4.6669047558px 0 #aaaaaa, 5.4447222151px 5.4447222151px 0 #aaaaaa, 6.2225396744px 6.2225396744px 0 #aaaaaa, 7.0003571337px 7.0003571337px 0 #aaaaaa, 7.7781745931px 7.7781745931px 0 #aaaaaa;
}

@media screen and (max-width: 500px) {
  .Header {
    font-size: calc(27px + 2vmin);
    margin: 5px;
  }  
}

@media screen and (max-width: 1000px) {
  .Header {
    font-size: calc(30px + 2vmin);
  }  
};
button {
  display: flex;
  border: 0;
  outline: none;
  border-radius: 3px;
  padding: 10px;
  font-size: 1rem;
  font-weight: bold;
  background: #21aa93;
  color: white;
  transition: all.5s ease;
  cursor: pointer;
  margin: 10px;
}

button:disabled {
  opacity: 0.5;
}

.ButtonWrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 10px;
}

.ButtonWrapper a {
  text-decoration: none;
}

.custom-file-input {
  color: transparent;
  width: 122px;
  height: 50px;
}

.custom-file-input::-webkit-file-upload-button {
  visibility: hidden;
}

.custom-file-input::before {
  content: 'Upload file';
  color: white;
  display: inline-block;
  background: #21aa93;
  border-radius: 3px;
  margin: 10px;
  padding: 10px;
  outline: none;
  white-space: nowrap;
  -webkit-user-select: none;
  cursor: pointer;
  font-weight: bold;
  transition: all.5s ease;
  font-size: 1rem;
}

.custom-file-input:active {
  outline: 0;
}

.custom-file-input:active::before {
  background: -webkit-linear-gradient(top, #e3e3e3, #f9f9f9);
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  transition: opacity 500ms;
  visibility: visible;
  opacity: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.popup {
  margin: 50px auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  background: #E0F2F1;
  border-radius: 5px;
  width: 30%;
  height: 20%;
  position: relative;
  transition: all 5s ease-in-out;
}

.popup h2 {
  margin-top: 0;
  color: #333;
  font-weight: bold;
}

.popup .close {
  position: absolute;
  top: 20px;
  right: 30px;
  transition: all 200ms;
  font-size: 30px;
  font-weight: bold;
  text-decoration: none;
  color: #333;
}

.popup .close:hover {
  color: #21aa93;
  cursor: pointer;
}

.popup .content {
  max-height: 30%;
  overflow: auto;
}

.content input {
  width: 500px;
  margin: 20px;
}

.SocialMediaWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  height: 100px;
}
.MemesWrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  margin-left: 5px;
  max-width: 1100px;
}

.MemesSection {
  margin: 0px;
  color: white;
  font-size: calc(30px + 2vmin);
  font-size: bold;
  text-shadow: 
  1px 1.5px 0 #333, 
  0px 1.5px 0 #333, 
  -1.5px -1.5px 0 #333, 
  -1.5px -1.5px 0 #333, 
  -1.5px 1.5px 0 #333, 
  1.5px -1.5px 0 #333, 
  0.6px 0.6px 0 #aaaaaa, 
  1.2px 1.2px 0 #aaaaaa, 
  1.8px 1.8px 0 #aaaaaa, 
  2.4px 2.8px 0 #aaaaaa, 
  3.0px 3.5px 0 #aaaaaa, 
  3.6px 3.6px 0 #aaaaaa, 
  4.2px 4.2px 0 #aaaaaa, 
  4.8px 4.8px 0 #aaaaaa, 
  5.4px 5.4px 0 #aaaaaa, 
  6.0px 6.0px 0 #aaaaaa;
}

.MemeCollection {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.MemeContainer {
    display: flex;
    max-width: 110px;
    height: 110px;
    border: 0.5px solid;
    margin: 3px;
}

.MemesSection {
    margin-bottom: 30px;
}

img {
    width: 100%;
    object-fit: cover;
    cursor: pointer;
}

.footer {
  padding: 20px;
  background-color: #092532;
  color:#E0F2F1;
}

.footer a {
  color: #21aa93;
}

.footer a:hover {
  opacity: .6;
}
.PreviewWrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 600px;
    width: 600px;
    max-width: 600px;
    max-height: 600px;
    color: transparent;
    background-color: white;
    border-radius: 5px;
    margin: 10px;
}    

.PreviewWrapper img {
    width: 95%;
    height: 95%;
    object-fit: contain;
    text-indent:-9999px;
    border: none;
}

.PreviewWrapper img:not([src]) {
    visibility: hidden;
 }

 @media screen and (max-width: 1100px) {
    .PreviewWrapper{
         width: 400px;
         max-width: 400px;
         height: 380px;
    }
 }
.EditorWrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 90%;
  width: 100%;
  margin-top: 20px;
  border-radius: 5px;
  min-width: 370px;
}

.EditorWrapper * + * {
  margin-top: 30px;
}

.EditorWrapper input {
  font-size: 1rem;
  box-sizing: border-box;
  border: none;
  padding: 5px;
  height: 35px;
  width: 80%;
  border-radius: 5px;
}

@media screen and (max-width: 1100px) {
  .EditorWrapper {
    width: 420px;
    height: 380px;
  }

}
